.logo {
    width: 215px;
    height: 55px;
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;

    &--small {
        @include vp-767 {
            width: 35px;
            height: 35px;
            
            & .logo__description {
                display: none; 
            }
        }
    }

    &__description {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        margin-left: 15px;
    }

    &__title {
        font-family: 'Roboto Condensed';
        font-weight: 700;
        font-size: 25px;
        line-height: 29px;
        color: $color-alice-blue;
    }

    &__subtitle {
        font-family: 'Roboto';
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        color: rgba($color-default-white, 0.7);
    }
}