.banner {
    background-image: $black-peral-to-black-peral, url('/img/main_bg_desktop.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 880px;

    @include retina {
        @media (min-width: $vp-1024 - 1) {
            background-image: $black-peral-to-black-peral, url('/img/main_bg_desktop@2x.jpg');
        } 

        @include vp-1023 {
            background-image: $black-peral-to-black-peral, url('/img/main_bg_tablet@2x.jpg');
        }

        @include vp-767 {
            background-image: $black-peral-to-black-peral, url('/img/main_bg_mobile@2x.jpg');
        }
    }

    @include vp-1023 {
        height: 640px;
        background-image: $black-peral-to-black-peral, url('/img/main_bg_tablet.jpg');
    }

    @include vp-767 {
        height: 560px;
        background-image: $black-peral-to-black-peral, url('/img/main_bg_mobile.jpg');
    }



    &__block {
        padding: 305px 0 280px 125px;

        @include vp-1023 {
            padding: 180px 0 205px;
        }

        @include vp-767 {
            padding: 150px 0 190px;
        }
    }   

    &__title {
        font-family: 'Roboto Condensed';
        font-weight: 700;
        font-size: 80px;
        line-height: 94px;
        letter-spacing: 0.07em;
        text-transform: uppercase;
        color: $color-alice-blue;
        margin: 0 0 40px;

        @include vp-1023 {
            font-size: 64px;
            line-height: 75px;
            margin: 0 0 20px;
        }

        @include vp-767 {
            font-size: 36px;
            line-height: 42px;
        }
    }

    &__text {
        font-family: 'PT Mono';
        text-transform: lowercase;
        font-weight: 400;
        font-size: 22px;
        line-height: 155%;
        color: $color-alice-blue;
        margin-bottom: 40px;
        max-width: 685px;

        @include vp-1023 {
            margin-bottom: 50px;
        }

        @include vp-767 {
            font-size: 14px;
        }
    }

    &__btn {
        width: 410px;
        text-align: center;

        @include vp-1023 {
            margin-left: 45px;
        }

        @include vp-767 {
            margin: 0 auto;
            display: block;
            max-width: 270px;
            width: 100%;
        }
    }
}

